<template>
  <div>
    <Tabs
      :dataSource="tabs"
      :defaultTab="defaultTab"
      @onClick="handleClick"
    ></Tabs>
    <router-view></router-view>
  </div>
</template>

<script>
import Tabs from "@/components/Tabs";
export default {
  components: {
    Tabs,
  },
  data() {
    return {
      defaultTab: "",
      tabs: [
        {
          name: "登入",
          key: "sign-in",
        },
        {
          name: "註冊",
          key: "sign-up",
        },
      ],
    };
  },
  methods: {
    handleClick(key) {
      this.$router.push(`/member/user/${key}`);
    },
  },
  watch: {
    "$route.path": {
      immediate: true,
      handler(value) {
        this.defaultTab = value.replace("/member/user/", "");
      },
    },
  },
};
</script>

